import jQuery from 'jquery';
import 'animate.css';
import 'corenav/coreNavigation.css';
import 'corenav';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel';
import onaftLogo from '../img/onaft.png';

export default function ResultSection(props) {
    return (
        <div id="result-part" className="content" role="tabpanel" aria-labelledby="result-part-trigger">
                <section id="results">
                    <div className="container">

                        <div className="card">
                            <div className="card-body">
                                <h5 className="card-title">Результат тестування: {props.result.title}</h5>
                                <p className="card-text ">{props.result.description}</p>
                            </div>
                        </div>

                        <div className="row mt-3">
                            <div className="col-lg-4 wow fadeInLeft" data-wow-duration=".2s" data-wow-delay=".2s">
                                <div className="result-box">
                                    <span className="lnr lnr-license"></span>
                                    <h4>Професії що вам підходять:</h4>
                                    <p>- Вчитель </p>
                                    <p>- Контент мейкер </p>
                                    <p>- Редактор </p>
                                </div>
                            </div>
                            <div className="col-lg-4 wow fadeInLeft" data-wow-duration=".2s" data-wow-delay=".4s">
                                <div className="result-box">
                                    <span className="lnr lnr-pie-chart"></span>
                                    <h4>Сфери діяльності:</h4>
                                    <p>- Видання</p>
                                    <p>- Школи та вищі навчальні заклади</p>
                                </div>
                            </div>
                            <div className="col-lg-4 wow fadeInLeft" data-wow-duration=".2s" data-wow-delay=".6s">
                                <div className="result-box">
                                    <span className="lnr lnr-graduation-hat"></span>
                                    <h4>Вузи, які ви можете обрати:</h4>
                                    <p>- Одеський педагогічний університет</p>
                                    <p>- Університет ім. Мечнікова</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                <section id="cards">
                    <div className="svg-wave">
                        <svg xmlns="http://www.w3.org/2000/svg" width="100%" viewBox="0 0 3600 248">
                            <path
                                    d="M3601,31.227S2736.31,201.97,1661,72.2C547.345-62.2,0,32.227,0,32.227V343H3602Z">
                            </path>
                        </svg>
                    </div>
                    <div className="container wow fadeInUp" data-wow-duration=".2s" data-wow-delay=".4s">
                        <h2>Вищі навчальні заклади Одеси</h2>
                        <div className="row">
                            <div className="col-lg-4">
                            
                                {props.result.specialties.map(specialty => {
                                    return ( 
                                        <div className="card border-success" >
                                            <h1>{specialty.title}</h1>
                                            {specialty.universities.map(university => {
                                                return (
                                                    <div >
                                                        <img src={onaftLogo} className="card-img-top" alt="ОНАХТ"/>
                                                        <div className="card-body">
                                                            <h5 className="card-title">{university.title}</h5>
                                                            <p className="card-text"><b>Адреса:</b>{university.location}</p>
                                                            <p className="card-text"><b>Повна назва:</b>{university.description}</p>
                                                            <p className="card-text"><b>Рейтинг:</b>{university.rate}</p>
                                                            <div className="result-box">
                                                                <span className="lnr lnr-graduation-hat"></span>
                                                                <h4>Перелік кафедр</h4>
                                                                {university.cathedraList.map(cathedra => {
                                                                    return (
                                                                        <p>- {cathedra.title}</p>
                                                                    )
                                                                })}
                                                            </div>
                                                            <a href="#" className="btn btn-primary">Детальніше</a>
                                                        </div>
                                                    </div>
                                                )
                                            })}
                                        </div>
                                    )
                                })}
                                
                            </div>
                            <div className="col-lg-4">

                             
                                <div className="card">
                                    <img src="assets/img/onaft.png" className="card-img-top" alt="ОНАХТ"/>
                                    <div className="card-body">
                                        <h5 className="card-title">Одеська Національна академія харчових технологій</h5>
                                        <p className="card-text"><b>Адреса:</b> вул. Канатна, 112, м. Одеса, Україна,
                                            65039</p>
                                        <p className="card-text"><b>Телефон:</b> (048) 712 4088</p>
                                        <p className="card-text"><b>Сайт:</b> https://www.onaft.edu.ua/</p>
                                        <a href="#" className="btn btn-primary">Детальніше</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="d-flex justify-content-center mt-5 mb-3">
                        <a href="/" className="btn btn-primary">Перейти до інших тестів</a>
                    </div>
                </section>
            </div>
    );
}